<template>
  <div>
    <template>
      <material-card
        color="accent"
        full-header
      >
        <template #heading>
          <v-tabs
            v-model="tab"
            background-color="transparent"
            slider-color="white"
            class="pa-8"
          >
            <v-tab
              href="#menu-1"
              @click="getcanceled"
            >
              CANCEL
            </v-tab>
            <v-tab
              href="#menu-2"
              @click="getDelivered"
            >
              COMPLETE
            </v-tab>
          </v-tabs>
        </template>

        <v-tabs-items v-model="tab">
          <v-tab-item id="menu-1">
            <cancel-history-view :data="canceled" />
          </v-tab-item>
          <v-tab-item id="menu-2">
            <delivered-history-view
              :data="delivered"
            />
          </v-tab-item>
        </v-tabs-items>
      </material-card>
    </template>
  </div>
</template>

<script>
  import axios from 'axios'
  // import HtmlPdf from '../Print/pdf.vue'
  import DeliveredHistoryView from './DeliveryHistory.vue'
  import CancelHistoryView from './CancelHistory.vue'

  export default {
    name: 'HistoryView',
    components: {
      CancelHistoryView,
      DeliveredHistoryView,
    },
    data () {
      return {
        selected: [],
        delivered: [],
        canceled: [],
        tab: null,
        search: '',
        sortable: false,
        rules: [
          value =>
            !value ||
            value.size < 2000000 ||
            'Avatar size should be less than 2 MB!',
        ],
        pickingup: [],
        pickedup: [],
        editedIndex: -1,
      }
    },

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },

    mounted () {
      this.getDelivered()
      this.getcanceled()
    },

    methods: {
      getDelivered () {
        this.selected = []
        axios.get('admin/order-exp/delivered-history').then(res => {
          this.delivered = res.data.data
        })
      },
      getcanceled () {
        this.selected = []
        axios.get('admin/order-exp/canceled-history').then(res => {
          this.canceled = res.data.data
        })
      },
    },
  }
</script>
